* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  overflow-x: hidden;
}
:root {
  --primaryColor: green;
  --primaryRGBA: rgba(0, 0, 0, 0.2);
  --mainGrey: #fafafa;
  --darkGrey: #5f5e5e;
  --mainWhite: #fff;
  --mainBlack: #222;
  --mainTransition: all 0.3s ease-in-out;
  --mainSpacing: 0.3rem;
  /* : #0e1c2e */
}

.text-title {
  font-family: "Calibri";
  font-weight: 300;
  text-transform: capitalize;
  padding: 10px;
}

.text-main {
  color: #d7392c;
  /* var(--primaryColor) */
}

body {
  font-family: "Calibri" !important;
  background: var(--mainGrey) !important;
  color: #0E1C2F !important;
  /* var(--mainBlack) */
}

.main-link {
  padding: 0.5rem 1rem;
  background:#f57403;
  /* var(--primaryColor); */
  color: var(--mainWhite);
  display: inline-block;
  text-transform: uppercase;
  /* letter-spacing: var(--mainSpacing); */
  /* outline: 2px solid var(--primaryColor); */
  /* outline-offset: 4px; */
  border: none;
  
}

.main-link:hover {
  transition: var(--mainTransition);
  background: var(--mainWhite);
  /* var(--darkGrey); */
  text-decoration: none;
  color: #d7392c;
  /* var(--primaryColor); */
  outline-color: var(--darkGrey);
}

